* {
    font-family: 'Montserrat', sans-serif !important;
}
button,a {
    cursor: pointer!important;
}
:root {
    --main_blue: #3789F8;
    --main_blue_hover: #0054C6;
    --main_blue_active: #0054C6;
}

.Button--lvl-tertiary {
    color: var(--main_blue)!important;
}
.Button--lvl-tertiary:hover {
    color: var(--main_blue_hover)!important;
}

.page_1_img {
    width: 100vw;
    height: calc(100vw / 3.6);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
}


.page_1_block {
    padding: 4vw 7vw 10px;
}
.page_1_block_s {
    display: flex;
    /*justify-content: space-between;*/
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: stretch;
}

a {
    color: var(--main_blue);
}

a:hover {
    color: var(--main_blue_hover);
}

.page_1_block a {
    font-size: 14px;
    color: var(--main_blue);
}
.page_1_block a:hover,
.page_1_block a:active{
    color: var(--main_blue_hover);
}

.page_1_card_img {
     width: 100%;
     overflow: hidden;
}
.page_1_card_img_img,
.page_2_card_img_img,
.page_3_card_img_img,
.page_4_card_img_img{
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    transition: all .3s ease-in-out!important;
}

.page_1_card {
    margin-bottom: 2vh;
    cursor: pointer;
}
.page_1_card:hover h3 {
    color: var(--main_blue_hover);
}
.page_1_card:hover .page_1_card_img_img {
    width: 130%;
    margin-left: -15%;
}


.page_1_card h3 {
    margin-top: 10px;
    margin-bottom: 4vh;
}



/*mobile*/
.page_1_card {
    max-width: 100%;
    min-width: 100%;
}
.page_1_card_img_img {
    height: calc(100vw / 1.78);
}
/*mobile*/
@media (min-width: 992px) {
    .page_1_card {
        max-width: 49%;
        min-width: 49%;
        margin-right: .7vw;
    }
    .page_1_card_img_img {
        height: calc(49vw / 1.78);
    }
}

@media (min-width: 1200px) {
    .page_1_card {
        max-width: 32%;
        min-width: 32%;
        margin-right: 1vw;
    }
    .page_1_card_img_img {
        height: calc(32vw / 1.78);
    }
}


.btn_1 {
    border-radius: 0;
    background: var(--main_blue);
    cursor: pointer;
    padding-top: 12px;
    padding-bottom: 12px;
}
.btn_1:hover {
    background: var(--main_blue_hover);
}
.btn_1:active {
    background: var(--main_blue_active);
}




.page_2_card {
    display: flex;
    justify-content: space-between;
    /*justify-content: flex-start;*/
    flex-wrap: wrap;
    align-items: stretch;
    margin-bottom: 2vh;
    cursor: pointer;
}

.page_2_card:hover h3 {
    color: var(--main_blue_hover);
}
.page_2_card:hover .page_2_card_img_img {
    width: 130%;
    margin-left: -15%;
}



.page_2_block_s {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    /*flex-direction: column;*/

}



/*mobile*/
.page_2_card_img {
    width: 100%;
    overflow: hidden;
}
.page_2_card_img1 {
    width: 100%;
    overflow: hidden;
}
.page_2_card_text {
    width: 100%;
    margin-bottom: 4vh;
}

.page_2_card {
    max-width: 100%;
    min-width: 100%;
}
.page_2_card_img_img {
    height: calc(100vw / 1.78);
}
.page_11_block {
    padding: 4vw 7vw 10px;
}
/*mobile*/
@media (min-width: 992px) {
    .page_2_card_img {
        width: 20%;
    }
    .page_2_card_img1 {
        width: 17%;
    }
    .page_2_card_text {
        width: 77%;
    }
    .page_2_card_img_img {
        height: calc(18vw / 1.78);
    }
    .page_2_card h3 {
        margin-top: 0!important;
    }
    .page_11_block {
        padding: 4vw 7vw 10px;
    }
}
@media (min-width: 1200px) {
    .page_2_card {
        max-width: 100%;
        min-width: 100%;
    }
    .page_2_card_img_img {
        height: calc(15vw / 1.78);
    }
    .page_2_card_img {
        width: 20%;
    }
    .page_2_card_img1 {
        width: 17%;
    }
    .page_2_card_text {
        width: 77%;
    }
    .page_2_card h3 {
        margin-top: 0!important;
    }
    .page_11_block {
        padding: 4vw 20vw 10px;
    }
}

.page_3_block {
    padding: 3vw 7vw 10px;
}

.page_3_block h2 {
    opacity: .7;
}

.page_3_card_img_img {
    height: calc(50vh + 50vw / 2.28);
}
.page_3_card_img {
    width: 100%;
}
.page_3_text {
    line-height: 30px;
}
.page_3_text {
    cursor: text;
}


.btn_2 {
    border-radius: 0;
    border: 1px solid var(--main_blue);
    cursor: pointer;
    padding-top: 12px;
    padding-bottom: 12px;
}
.btn_2:hover {
    border: 1px solid var(--main_blue_hover);
}
.btn_2:active {
    border: 1px solid var(--main_blue_active);
}

.btn_2_2 {
    border-radius: 0;
    border: 1px solid var(--main_blue);
    cursor: pointer;
    padding-top: 6px;
    padding-bottom: 6px;
}
.btn_2_2:hover {
    border: 1px solid var(--main_blue_hover);
}
.btn_2_2:active {
    border: 1px solid var(--main_blue_active);
}


.page_4_block_s {
    display: flex;
    /*justify-content: space-between;*/
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: stretch;
}


.page_4_card h4 {
    color: var(--main_blue);
}

.page_4_card h3 {
    opacity: .7;
    text-decoration: none!important;
    /*color: #000*/
}

.page_4_card {
    /*display: flex;*/
    /*justify-content: space-around;*/
    /*!*justify-content: flex-start;*!*/
    /*flex-wrap: wrap;*/
    /*align-items: stretch;*/
    margin-bottom: 2vh;
    cursor: pointer;

}
.page_5_card {
    margin-bottom: 6vh;
}
.page_4_card > * {
    text-decoration: none!important;
}
.page_5_card h3{
    min-height: 5vh;
}
/*mobile*/
.page_4_card,
.page_5_card {
    max-width: 100%;
    min-width: 100%;
}
.page_4_card_img_img {
    height: calc(100vw);
}
/*mobile*/
@media (min-width: 992px) {
    .page_4_card,
    .page_5_card{
        max-width: 49%;
        min-width: 49%;
        margin-right: .7vw;
    }
    .page_4_card_img_img {
        height: calc(50vw - 7vw);
    }
    .page_3_block {
        padding: 3vw 14vw 10px;
    }

}

@media (min-width: 1200px) {
    .page_4_card,.page_5_card
    {
        max-width: 24%;
        min-width: 24%;
        margin-right: .8vw;
    }
    .page_4_card_img_img {
        height: calc(25vw - 7vw);
    }
    .page_3_block {
        padding: 5vw 17vw 10px;
    }
}













.Search {
    width: auto;
    display: block;
}





.menu1 {
    display: none;
    max-height: 60px;
    padding: 0 7vw;
    background: var(--background_content);

}
.menu1 > div {
    display: flex;
    justify-content: space-between;
}

.logo {
    height: 60px;
    width: 60px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    cursor: pointer;
}
.logo2 {
    height: 6vw;
    width: 6vw;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    cursor: pointer;
}

.menu2 {
    display: flex;
    align-items: center;

}
.footer {
    /*min-height: 20vh;*/
    display: none;
    background: var(--search_bar_field_background);
    padding: 2vw 14vh 1vw;
    position: relative;
    margin-top: 4vh;
    text-align: center;
}
.page_1_img span {

    text-transform: uppercase;
    font-size: 12px;
    padding-left: 1vw;
    color:  #fff;
}
.page_1_img p {
    margin: 0!important;
    display: block;

    font-size: .6em;
    padding: 7px;
    font-weight: 600;
    background: var(--counter_primary_text);

}
@media (min-width: 992px) {
    .menu1 {
        display: block;
    }
    .Tabbar {
        display: none;
    }
    /*.PanelHeader {*/
    /*    display: none;*/
    /*}*/
    .Epic .Panel__in {
        padding-bottom: 0!important;
    }
    .Search {

        width: 20vw;
        display: inline-block;
    }
    .footer {
        display: block;
    }
    .page_1_img span {
        font-size: 22px;
    }
    .page_1_img p {
        font-size: 1em;
    }
}

.slidBtn1 {
    position: absolute;
    height: 100%;
    z-index: 2;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
    border: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    color: var(--background_content);
}

.slidBtn2 {
    position: absolute;
    height: 100%;
    z-index: 2;
    right: 0;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    border: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    color: var(--background_content);
}
.slidBtn1 .Button__content,
.slidBtn2 .Button__content{
    color: var(--background_content) !important;
}


.fot {
    display: flex;
    align-items: center;
    justify-content: space-between;
}


.sds svg {
    color: var(--main_blue)!important;
}

